"use client";

import { useState } from "react";
import { ChevronDown } from "lucide-react";
import { cn } from "@/lib/utils/cn";

const faqs = [
  {
    question: "Is this only about AI?",
    answer:
      "No, we focus on solving real business problems. AI is one tool in our toolkit, but we use it only when it delivers measurable value. We also build custom software, websites, and automation systems that don't require AI.",
  },
  {
    question: "How customized are your solutions?",
    answer:
      "Every solution is built specifically for your business. We don't use generic templatesâ€”we analyze your workflows, understand your constraints, and design systems that fit how you actually operate.",
  },
  {
    question: "Will my team understand how to use the new system?",
    answer:
      "Yes. Every project includes comprehensive training, documentation, and ongoing support. We design systems to be intuitive, and we work with your team throughout the process to ensure adoption.",
  },
  {
    question: "How long does a typical project take?",
    answer:
      "Project timelines vary based on scope, but most projects range from 4-12 weeks. We provide clear timelines in your proposal and keep you updated throughout the process via our project dashboard.",
  },
  {
    question: "Is this suitable for small and medium businesses?",
    answer:
      "Absolutely. We work with businesses of all sizes. Our solutions are designed to scale with you, whether you're a small team or a growing company. We focus on ROI and solutions that make sense for your budget.",
  },
];

export function FAQSection() {
  const [openIndex, setOpenIndex] = useState<number | null>(0);

  const toggleQuestion = (index: number) => {
    setOpenIndex(openIndex === index ? null : index);
  };

  return (
    <section id="faq" className="py-20 md:py-32">
      <div className="container mx-auto px-4">
        <div className="mx-auto max-w-3xl">
          <h2 className="mb-12 text-center text-3xl font-bold tracking-tight sm:text-4xl md:text-5xl">
            FAQs
          </h2>
          <div className="space-y-4">
            {faqs.map((faq, index) => (
              <div
                key={index}
                className="rounded-lg border-2 border-terminal-border-light bg-card transition-all"
              >
                <button
                  onClick={() => toggleQuestion(index)}
                  className="flex w-full items-center justify-between p-6 text-left"
                  aria-expanded={openIndex === index}
                >
                  <h3 className="pr-8 text-lg font-semibold">{faq.question}</h3>
                  <ChevronDown
                    className={cn(
                      "h-5 w-5 shrink-0 text-muted-foreground transition-transform",
                      openIndex === index && "rotate-180"
                    )}
                  />
                </button>
                {openIndex === index && (
                  <div className="border-t border-terminal-border-light px-6 pb-6 pt-4">
                    <p className="text-base text-muted-foreground">
                      {faq.answer}
                    </p>
                  </div>
                )}
              </div>
            ))}
          </div>
        </div>
      </div>
    </section>
  );
}
